* {
  font-family: 'Bad Script', cursive;
  color: white;
}

.App {
  text-align: center;
  background-image: url(./img/background.png);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  height: 100vh;
  width: 100vw;
}

.nav-link {
  color: black !important;
  margin: 10px 15px;
  font-size: x-large;
}

.brandname {
  color: white !important;
  border-bottom: 1px solid white;
  
}

.header {
  height: 10vh;
  
}

.rightside  {
  min-height: 90vh;
}

.rightside h1 {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.leftside  {
  min-height: 90vh;
  position: relative;
  padding: 0px 30px;
}

.leftside h1 {
  float: left;
  font-size: 4rem;
  font-weight: 300;
  position: relative;
  top: 30%;
  left: 30%;
  transform: translate(-30%, -50%);
}